// 
.breadcrumb{
    display: flex;
    padding: 8px 0;

    @include pad-lg(){
        margin-left: 39px;
    }

    @include phone-md(){
        margin-left: 15px;
    }

    li{
        margin-right: 5px;

        &:after{
            content: '/';
            margin-left: 5px;
        }

        a{
            color: #000;
            text-decoration: none;

            &:hover{
                color: $bg-red;
            }
        }
    }

    .breadcrumb-active{
        color: $bg-red;
    }
}

// tag
.tags{
    display: flex;
    padding: 0;

    @include pad-lg(){
        margin-left: 39px;
    }

    @include phone-md(){
        margin-left: 15px;
    }
}

// tag
.tag{
    a{
        font-size: 24px;
        line-height: 36px;
        color: $bg-red;
        margin-right: 16px;
        font-weight: bold;
        text-decoration: none;

        @include pad-md(){
            font-size: 16px;
            line-height: 25px;
        }

        @include phone-md(){
            line-height: 35px;
        }

        &:hover{
            color: $hover-color;
        }
    }
}

// 
.blog-content{
    display: flex;
    padding: 0;

    @include pad-lg(){
        margin-left: 39px;
    }

    @include phone-md(){
        flex-direction: column;
        margin-left: 0;
        padding: 0 15px;
    }

    .article-content{
        max-width: 825px;
        padding: 0;
        margin-right: 30px;

        @include pad-lg(){
            max-width: 650px;
        }

        @include pad-md(){
            max-width: 510px;
        }

        @include phone-md(){
            margin-right: 0;
            max-width: 100%;
        }
    
        .article-title{
            margin-bottom: 25px;
            
            li{
                display: flex;
                justify-content: space-between;
                align-items: center;

                // 
                h2{
                    font-size: 48px;
                    line-height: 95px;

                    @include pad-md(){
                    font-size: 32px;
                    line-height: 30px;
                    }
                }
    
                // 
                h3{
                    font-size: 28px;
                    line-height: 42px;

                    @include pad-md(){
                    font-size: 16px;
                    }
                }

                //
                h4{
                    font-size: 14px;
                    line-height: 24px;
                    color: #6C6C6C;
                }
            }
        }

        // 
        .img-box{
            height: 539px;
            @include bg-cover;
            margin-bottom: 24px;

            @include pad-md(){
                width: 510px;
            }

            @include phone-md(){
                width: 100%;
            }        
        }

        // 
        p{
            font-size: 20px;
            line-height: 30px;
            color: rgba(0, 0, 0, .6);
            margin-bottom: 40px;

            @include pad-md(){
                font-size: 16px;
                line-height: 24px;
            }
        }

        // 
        .share-link{
            display: flex;
            align-items: center;
            padding-bottom: 21px;
            border-bottom: 1px solid #707070;

            h3{
                font-size: 20px;
                line-height: 30px;
                margin-right: 20px;

                @include pad-md(){
                    font-size: 16px;
                    margin-right: 16px;
                }
            }

            li{
                img{
                    width: 24px;
                    height: 24px;
                    margin-right: 16px;
                }
            }
        }

        // /
        .page-link{
            padding-top: 24px;
            display: flex;
            justify-content: space-between;
            align-items: center;
            margin-bottom: 160px;

            @include phone-md(){
                margin-bottom: 30px;
                flex-direction: column;
                align-items: start;
            }

            li{
                a{
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    text-decoration: none;
                    color: #000;
    
                    &:hover{
                        color: $bg-red;
                    }
    
                    h3{
                        padding: 0 12px;
                    }
                }
            }
        }

        // 
        .reserve{
            flex-direction: row-reverse;

            @include phone-md(){
                flex-direction: row;
            }
        }
    }

    // tag



    .tag-list{
        width: 30%;
        
        @include phone-md(){
            width: 100%;
            margin-bottom: 56px;
        }

        li{
            display: flex;
            align-items: center;
            padding: 8px 0;

            @include pad-md(){
                padding: 0;
            }

            img{
                margin-right: 12px;

                @include pad-md(){
                    margin-right: 3px;
                }
            }

            h3{
                font-size: 28px;
                line-height: 42px;
                color: $bg-red;
                font-weight: bold;

                @include pad-md(){
                    font-size: 20px;
                    line-height: 42px;
                }
            }
        }

        .tag-inline{
            display: inline-block;
        }

    }

}
